@font-face {
  font-family: Gilroy;
  src: url("../../../../assets/fonts/gilroy-regular.ttf");
}
h1,
h2,
h3,
h4,
p,
li,
ul,
input,
button {
  margin: 0;
  padding: 0;
  font-family: Gilroy;
}

ul,
ol {
  list-style: none;
}
.main-container {
  min-height: 100vh;
  padding-top: 24px;
  padding-left: 24px;
  padding-right: 24px;
  padding-bottom: 77px;
  background-color: #f5f7fb;
}
.table-board {
  display: flex;

  justify-content: right;
}
.title-page {
  font-weight: 600;
  font-size: 28px;
  line-height: 29px;
  color: #030306;
}
