.card {
  padding: 10px 12px 0 12px;
  min-width: 330px;
  border-radius: 16px;
  background-color: #ffffff;
}
.header-card {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.title {
  margin-left: 12px;
  font-family: Gilroy;
  font-weight: 600;
  font-size: 16px;
  line-height: 19px;
  color: #000000;
}
